.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

* {
  list-style: none;
}

body {
  margin: 0;
}
div {
  box-sizing: border-box;
}

nav {
  position: fixed;
  display: flex;
  justify-content: space-around;
  background: rgba(0, 0, 0, 0.8);
  color: white;
  width: 90%;
  height: 65px;
  left: 0;
  right: 0;
  top: 20px;
  margin: auto;
  padding: 10px;
  z-index: 5;
}

.nav-list {
  margin-top: 10px;
}
.nav-list ul {
  display: inline-block;
}
.nav-list li {
  display: inline-block;
  font-size: 15px;
  margin: 0 7px;
}
#menu {
  display: none;
}

.jumbo {
  width: 100%;
  height: 600px;
  background: #eee;
  color: white;
  padding-top: 250px;
  background-image: url('main_img.jpg');
  background-size: cover;
}
.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 250px 180px 250px;
  grid-gap: 20px;
  grid-template-areas: 
  "a a b"
  "c . b"
  "c d d"
  ;
}

.grid-1 {
  grid-area: a;
  background: #DEF9FF;
  overflow: hidden;
}
.grid-2 {
  grid-area: b;
  background: #FFF0F0;
}
.grid-3 {
  grid-area: c;
  background: #FDF0FF;
}
.grid-4 {
  grid-area: d;
  background: #E1FFE0;
  overflow: hidden;
}
.grid-5 {
  background: #FFF185;
}

.bestseller {
  background: black;
  color: white;
}
.card {
  color: black;
  margin-bottom: 25px;
  border-radius: 0;
}

.rank {
  position: absolute;
  width: 25px;
  height: 25px;
  background: darkgray;
  color: white;
}

.cart, .detail {
  margin-top: 150px;
}

footer {
  height: 150px;
  background: black;
  color: white;
  padding-top: 40px;
}

.alert {
  background: #FAFABE;
    padding: 20px;
    margin-top: 20px;
    border-radius: 5px;
    width: 100%;
    margin: auto;
}
.alert p {
  margin-bottom: 0;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
